body{
    font-family: 'Roboto', sans-serif;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid black;
    background-color: rgb(0,0,0);
    background-color: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(162,162,162,1) 49%, rgba(0,0,0,1) 100%);
