@media screen and (max-width: 530px){
    body{
        width: 530px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
    }
}
.nav-bar{background:#d2691e;padding:15px;display:flex;justify-content:space-between;align-items:center}.nav-bar .more-options:hover{cursor:pointer;color:#fff}.nav-bar .logo{display:flex;transition:.5s ease-in-out}.nav-bar .logo::after{position:absolute;content:"Load Popular Movies";margin-top:80px;margin-left:15px;transform:rotate(-5deg);visibility:hidden}.nav-bar .logo:hover{cursor:pointer;transform:translateY(-20px)}.nav-bar .logo:hover::after{visibility:visible;transform:translateY(-25px)}@media screen and (max-width: 530px){.nav-bar{position:relative}}
.movie-card{padding:5px;background:#615f5f;background:linear-gradient(0deg, #615f5f 0%, #a2a2a2 49%, #555353 100%)}.movie-card .a2f{position:absolute;margin-top:385px;margin-left:235px;color:#5f9ea0;z-index:1}.movie-card .title{background:rgba(0,0,0,.493);padding:5px;position:absolute;margin-top:-35px;color:#fff;width:290px}
.data-field{background:#292828;display:flex;flex-direction:column;align-items:center;border:2px solid #fff}.data-field .top-btns input{font-size:17px;padding:10px 15px;border-radius:20px}.data-field .movie-display{padding:3px;display:grid;grid-template-columns:310px 310px 310px 310px;grid-gap:3px 10px;gap:3px 10px}.data-field .footer-area{background:#000;color:#fff;display:flex;justify-content:center;align-items:center}.data-field .btn-def{color:#fff;border:unset;margin:5px;padding:15px;border-radius:20px;background:#d2691e}.data-field .btn-def:hover{cursor:pointer;background:#53321a}@media screen and (max-width: 530px){.data-field{display:flex;flex-direction:column;margin-left:0px;position:relative}.data-field .top-btns{position:relative;left:60px;width:78.5%}.data-field .movie-display{padding:5px;display:grid;grid-template-columns:200px 200px;grid-gap:10px 10px;gap:10px 10px}.data-field .movie-display .movie-card .a2f{margin-left:120px;margin-top:180px}.data-field .movie-display .movie-card .title{width:186px}.data-field .movie-display .movie-card .image .link .poster{width:188px;height:280px}}
.movie-details{display:flex;flex-direction:column;justify-content:center;padding:5px 0px;background:rgba(7,7,7,.288)}.movie-details .backdrop .backdrop-img{z-index:1;height:630px;width:1260px}.movie-details .movie{position:absolute;padding:5px;z-index:1;background:rgba(0,0,0,.247);width:1230px;display:flex}.movie-details .movie .poster{padding:0px 10px}.movie-details .movie .movie-text{color:#cecece;background:rgba(0,0,0,.281);padding:0px 15px}.movie-details .movie .movie-text .title{color:#d2691e;font-family:"Cormorant Unicase",serif;box-shadow:2px 2px 5px 1px #000;text-shadow:-1px -1px 0 #000,1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000;font-size:30px;margin-top:5px;margin-bottom:15px;padding-left:5px}.movie-details .movie .movie-text .plot{text-shadow:-1px -1px 0 #000,1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000;padding:2px;font-size:1.5rem;font-family:"Redressed",cursive}@media screen and (max-width: 530px){.movie-details{padding:1px;background:rgba(7,7,7,.288);z-index:1;display:flex;justify-content:center;align-items:center;flex-direction:column}.movie-details .backdrop{padding:5px 0px;border-bottom:3px dotted #fff;margin-bottom:5px}.movie-details .backdrop .backdrop-img{z-index:1;height:30%;width:100%}.movie-details .movie{position:relative;top:0px;left:0px;padding:1px;z-index:5;background:rgba(0,0,0,.247);width:auto;display:flex;flex-direction:column;align-items:center}.movie-details .movie .poster{box-shadow:2px 2px 5px 1px #000}.movie-details .movie .movie-text{color:#cecece;background:rgba(0,0,0,.281);padding:0px 5px;display:flex;flex-direction:column;justify-content:center;align-items:center}.movie-details .movie .movie-text .title{display:flex;flex-direction:column;justify-content:center;align-items:center;color:#d2691e;font-family:"Cormorant Unicase",serif;box-shadow:2px 2px 5px 1px #000;text-shadow:-1px -1px 0 #000,1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000;font-size:30px;margin-top:5px;margin-bottom:15px;padding-left:5px}.movie-details .movie .movie-text .plot{text-shadow:-1px -1px 0 #000,1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000;padding:2px;font-size:1.5rem;font-family:"Redressed",cursive;width:auto}}

